<template>
<div id='resume'>
    <pre class='styleEditor'>
        <div v-html="heightCode"></div>
        <div v-html="styleCode"></div>
    </pre>
    <div class='resumeEditor'>
        <div v-html="resumeCode"></div>
        <div v-html="styleCode1"></div>
    </div>
</div>
</template>

<script>
let str=` 
                /* 最近找工作了，准备一份简历！*/

                /* 首先给所有元素加上过渡效果

                * {
                transition: all .2s;
                }*/

                /* 白色背景太单调了，我们来点背景 */

                html {
                    color: rgb(222,222,222); background: rgb(0,43,54);
                }

                /* 文字离边框太近了 */

                .styleEditor {
                    padding: .5em;
                    border: 1px solid;
                    margin: .5em;
                    overflow: auto;
                    width: 45vw; height: 90vh;
                }

                /* 代码高亮 */

                .token.selector{ color: rgb(133,153,0); }
                .token.property{ color: rgb(187,137,0); }
                .token.punctuation{ color: yellow; }
                .token.function{ color: rgb(42,161,152); }

                /* 加点 3D 效果呗 */

                html{
                    perspective: 1000px;
                }
                .styleEditor {
                    position: fixed; left: 0; top: 0;
                    -webkit-transition: none;
                    transition: none;
                    -webkit-transform: rotateY(10deg) translateZ(-100px) ;
                            transform: rotateY(10deg) translateZ(-100px) ;
                }
                /* 接下来我给自己准备一个编辑器 */
                .resumeEditor{
                    position: fixed; left: 48%; top: 0;
                    padding: .5em;  margin: .5em;
                    width: 48vw; height: 90vh;
                    border: 1px solid;
                    background: white; color: #222;
                    overflow: auto;
                    word-break:break-all;
                }`
import marked from 'marked';
import Prism from 'prismjs';
let res=` \n  ### Name: 陈雷超
\n  ## 工作经历：
\n  ### 项目一：Uhoou网站和相关api开发 
\n  开发工具：Visual studio 2015 
\n  开发时间：2017.09——2017.11 
\n  项目描述：本人负责浙江浙江游猴官网和客户平台前端开发，同时期间为 其宝宝电玩城app开发api接口。此app已经在app store和Android平台上 线。了解网站ICP和文网文申请。和hybrid app开发相关流程。
\n  技术架构：选用相关js框架插件进行开发，达到快速开发目的。后台.net。 
\n  遇到问题：为了负责api的开发。自己硬生生去看.net官方文档和相关C#语 法，从而完成任务。需求的不定的更改，让工作变得复杂，不过上线意味一 切值得。
\n  实现细节：快速解决问题的能力，和寻找答案的能力。当无法遇见解决问题的时 候，通常去google或相关社区去寻找答案。从前到后，才发现全栈的味道十足。 
\n  总结收获：1、开始习惯看英文文档,用英文做笔记。2、熟悉前后端过程。 3、知道如何在cocos中内嵌网页，算是了解hybrid app开发流程吧。 4、好玩的技术都没有用上，比较无奈，也明白了技术是为需求服务的 
\n  [官网](http://www.uhoou.com/) [客户网站](http://www.game719.com/)  
\n \n
\n  ### 项目二：重构VUE 社区 
\n  项目描述：VUE全家桶来重构vue社区 
\n  开发时间：2017.12，历时 7 天 
\n  技术要点：Vue、 Vuex、Vue-router、SASS、Webpack2.0、LearnCloud 
\n  总结收获：熟悉vue全家桶，和相关使用经验吧。
\n  其中也加入了Vue版本的TODO。
\n  vue的生命周期真的重要，在制作过程中，缺乏了解，然后一度卡壳。然后看文档，就解决了。
\n  vue-router 难用，在跑例子过程中，一度跑例子失败。现在也就用的比较简单。主要采用子组件组成主组件进行使用。后期加强吧。
\n  vuex不得不说一点，文档是不太好读，所以建议学习中，遇见不清楚的、不知道的，去看看别人的博客。
\n  vuex重要的一点，不要省state。自己在项目制作过程中，就因为想省一个state，然后一天也没有搞定流程。最后还是加上了。（真的想拍死自己!）
\n  vuetify 这个UI framework 是真的难用! 用的后来，还不如自己多写几个组件。
\n  [预览&源码](https://github.com/komolei/vueCopies) 
\n \n
\n  ### 项目三：在线便利贴 
\n  项目描述：基于express而开发的在线可编辑的便利贴 
\n  开发时间：2017.12，历时7天 
\n  技术要点：ES6,EXPRESS,FETCH sub\\push 事件 
\n  功能：signUp、logIn and logOut // GitHub OAuth
\n  CRUD operations
\n  chatbot
\n  收获：订阅发布事件的使用，socketIo的使用。
\n  [预览&源码](https://github.com/komolei/onlineNote) 
\n \n
\n  ### 项目三：代办事项 WebApp (React)
\n  开发工具：VS code 
\n  开发时间：2017.09，历时 10 天 
\n  项目描述：本项目是一个在线 TodoList 应用。
\n  该项目具有注册 登录，支持 输入事项的增删改，云端数据同步的功能。
\n  技术架构：前端架构选用 React，Redux，React-router，进行单页开发， 后端采用 Learncloud 进行数据的支持，采用最新的 ES6 语法，追求最佳的 实践 
\n  遇到问题：React独创的JSX语法，开始使用的时候，接受比较困难。在项目中结合Redux，真的是一件比较难的事情。啃的比较头疼吧。同时，一个 小项目使用React-router，真的也是大而笨重。个人而言，还是redux的实 践较难。 
\n  实现细节：看文档和阅读Redux源码。反复尝试，完成功能。
\n    
\n  ### 项目四：在线简历编辑器 
\n  项目描述：其是一个在线简历编辑的应用，通过用户的注册和登录，来编辑 和存放其的简历。 
\n  开发时间：2017.06，历时 7 天 
\n  技术要点：Vue、 Vuex、Vue-router、SASS、Webpack2.0、LearnCloud 
\n  总结收获：1、如何设计 store 来存放 state？
\n  根据组件的层次去设计，还是 统一建立呢？ 根据组件的不同，分别建立不同的数据源，存放到 store 中。不可行。
\n  当不同组件操作同一份数据源的时候，A 组件不光要修改自己的 state，还要 通过发 action 来修改 B 组件的 state，很麻烦；
\n  统一建立：为了在不同组件 调用的时候，不必反复 action
\n  2、组件的结构设计：一定要想好再开始，不然后面修改，涉及面太广 
\n  [预览](komolei.github.io/vue/resumer/dist/#/)[源码](https://github.com/komolei/vue/tree/master/resumer)
\n 具体请看[PDF](https://github.com/komolei/resume/blob/master/resume1221.pdf),谢谢
`
marked.setOptions({
  renderer: new marked.Renderer(),
  gfm: true,
  tables: true,
  breaks: false,
  pedantic: false,
  sanitize: false,
  smartLists: true,
  smartypants: false
});
export default {
    name: 'name',
    data() {
        return {
            code: '',
            context: [str,
               res ],
            style_code: '',
            resume: '',
        }
    },
    methods: {
        timerControl: async function() {
            await this.changeText();
            await this.changeResume();
        },
        changeResume: function() {
        return new Promise((res,rej)=>{
                    let le = 0;
                let timer = setInterval(() => {
                    this.resume = this.context[1].substring(0, le)
                    if (le === this.context[1].length) {
                        window.clearInterval(timer);
                        console.log(`写完了1`);
                        res(1);
                    } else {
                        le++;
                    }
                }, 0)
       })
        },
        changeText: function() {
            return new Promise((res, rej) => {
                let le = 0;
                let timer = setInterval(() => {
                    this.code = this.context[0].substring(0, le)
                    if (le === this.context[0].length) {
                        window.clearInterval(timer);
                        console.log(`写完了1`);
                        res(1);
                    } else {
                        le++;
                    }
                }, 0)
            })
        },
    },
    computed: {
        styleCode: function() {
            return `<style>${this.code}</style>`
        },
        heightCode: function() {
            return Prism.highlight(this.code, Prism.languages.css)
        },
        resumeCode: function() {
            return marked(this.resume);
        },
         styleCode1: function() {
            return `<style>${this.resume}</style>`
        },
    },
    created() {
        this.timerControl()
    }
}
</script>


<style lang="less" scoped>
#resume {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.resumeEditor{
    // text-align:center;
    h3{
        margin:1rem;
    } 
    p{
        padding: .2em;
        // margin:1em;    
    }
}

</style>


